#laquestCareer {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
}

.laquest-career-container {
  width: 100%;
  position: relative;
}

.laquest-career-container {
  display: grid;
  grid-template-columns: 0.4fr 0.3fr 0.3fr;
  gap: 1rem;
  align-items: center;
}

.laquest-career-container .stories-heading-sec {
  padding-bottom: 1rem;
}

.laquest-career-container .stories-heading-sec span {
  line-height: 1.3;
}

.career-dec {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  padding-right: 1rem;
}

.laquest-career-container .study-box {
  justify-content: space-evenly;
}

.laquest-career-container .study-box p {
  height: 35px;
}

.laquest-career-container .study-box span {
  font-size: 16px;
  line-height: 1.4;
}

.employe-details-container .story-heading-sec span {
  font-size: 22px;
  text-transform: capitalize;
}

.employe-details-container .employe-position {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
}

.employe-details-container .story-heading-sec {
  padding-bottom: 0.5rem;
}

.impact-list-container ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.impact-list-container ul li {
  font-family: "Poppins", sans-serif !important;
  font-size: 20px;
  padding-bottom: 1rem;
  border-bottom: 2px solid rgb(128 128 128 / 15%);
  margin-bottom: 1rem;
}

.impact-section .google-tools-container {
  grid-template-columns: 1fr 0.8fr;
}

#employeeSpotlight {
  width: 100%;
  position: relative;
  margin-bottom: 10rem;
}

.employee-spotlight-sec {
  width: 100%;
  position: relative;
}

.employee-spotlight-sec .success-story-text-btn-sec p {
  font-size: 16px;
  margin-bottom: 1rem;
}

.name-position-sec p {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  font-weight: 700;
}
.name-position-sec span {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

.read-story-btn-sec button {
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
  border: none;
  background: transparent;
  padding: 0;
  padding-top: 1.5rem;
  cursor: pointer;
}

.employee-spotlight-sec .story-content-sec {
  height: 350px;
}

.roderick-img-sec {
  background-image: url("../../assets/images/career/roderick.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100%;
}
.gladys-img-sec {
  background-image: url("../../assets/images/career/gladys.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 100%;
}
.spotlight-img-sec {
  background-image: url("../../assets/images/career/spotlight.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
}

.employee-spotlight-sec .success-story-text-btn-sec {
  padding-top: 2rem;
}

#apportunities {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-bottom: 10rem;
}

.career-apportunities-sec {
  position: relative;
  width: 100%;
}

.apportunities-first-grid {
  margin-bottom: 5rem;
}

.career-apply-btn {
  margin-left: 1rem;
}

.career-apportunities-sec button {
  gap: 2.5rem;
}

.potential-img-sec {
  background-image: url("../../assets/images/case-study/local-govt-img.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100%;
  transform: scaleX(-1);
}

.career-potential-sec .local-government-inner-sec .story-content-sec {
  background-color: #f8f9fa;
  background-image: none;
}

.story-heading-sec span {
  text-transform: none;
}

.career-potential-sec .success-story-text-btn-sec p {
  font-size: 18px;
  padding-right: 3rem;
}

.bullets {
  margin-top: 4rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.bullets i {
  color: #545454;
}

.career-potential-sec .story-heading-sec {
  border: none;
}

@media screen and (max-width: 480px) {
  #laquestCareer {
    padding-bottom: 5rem;
  }

  .laquest-career-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .laquest-career-container .stories-heading-sec {
    padding-left: 0rem;
  }

  .roderick-img-sec {
    height: 500px;
  }

  .gladys-img-sec {
    height: 500px;
  }

  .employe-details-container .succes-story-container {
    gap: 3rem;
  }

  .impact-section .google-tools-container {
    grid-template-columns: 1fr;
  }

  .impact-section .stories-heading-sec {
    padding-left: 0;
  }

  .spotlight-img-sec {
    height: 500px;
    width: 100%;
  }

  .employee-spotlight-sec .first-story-sec {
    display: flex;
    flex-direction: column-reverse;
  }

  .potential-img-sec {
    height: 500px;
    width: 100%;
    background-position: left;
  }

  #apportunities {
    padding-bottom: 5rem;
  }

  .career-potential-sec .first-story-sec {
    display: flex;
    flex-direction: column-reverse;
  }

  .impact-section .google-tool-heading-grid-item {
    padding-left: 0rem;
  }

  .employee-spotlight-sec .stories-heading-sec {
    padding-left: 0rem;
  }

  .bullets {
    margin-top: 1rem;
  }
  .employee-spotlight-sec .success-story-text-btn-sec {
    padding-top: 1rem;
  }
  .employee-spotlight-sec .success-story-text-btn-sec p {
    font-size: 16px;
    margin-bottom: 0rem;
  }

  .employee-spotlight-sec .story-content-sec {
    height: 375px;
  }

  #footer {
    padding-top: 3rem;
  }

  .career-potential-sec .success-story-text-btn-sec p {
    font-size: 18px;
    padding-right: 1rem;
  }
}
